import React,{useState,useEffect} from 'react'
import { BrowserRouter as Router, Route, Switch,Redirect,useLocation } from 'react-router-dom'
import { useAuth } from '../context/AuthContext'
import ForgotPasswordPage from '../pages/ForgotPasswordPage'
import Homepage from '../pages/Homepage'
import Loginpage from '../pages/Loginpage'
import NotfoundPage from '../pages/NotfoundPage'
import Profilepage from '../pages/Profilepage'
import ProtectedPage from '../pages/ProtectedPage'
import Registerpage from '../pages/Registerpage'
import ResetPasswordPage from '../pages/ResetPasswordPage'

export default function AppRouter(props) {
  const [serverUrl, setServerUrl] = useState('/');
  useEffect(() => {
    if(window.location.href.indexOf('themiraj.github.io') > -1){
      setServerUrl('/google-auth/')
    }
    return () => {
      
    }
  }, [serverUrl])
  return (
    <>
      <Router>
        <Switch>
          <Route exact path={`${serverUrl}`} component={Homepage} />
          <ProtechRoute exact path={`${serverUrl}login`} component={Loginpage} />
          <ProtechRoute exact path={`${serverUrl}register`} component={Registerpage} />
          <ProtechRoute exact path={`${serverUrl}profile`} component={Profilepage} />
          <ProtechRoute exact path={`${serverUrl}protected-page`} component={ProtectedPage} />
          <ProtechRoute exact path={`${serverUrl}forgot-password`} component={ForgotPasswordPage} />
          <ProtechRoute exact path={`${serverUrl}reset-password`} component={ResetPasswordPage} />
          <Route exact path='*' component={NotfoundPage} />
        </Switch>
      </Router>
    </>
  )
}

function ProtechRoute(props){
  const {currentUser} = useAuth();
  const location = useLocation();
  const { path } = props 

  if(path === '/login' || path === '/register' || path === '/forgot-password' || path === '/reset-password') {
    return currentUser ?
    ( 
      <Redirect to={location.state?.from ?? '/profile'} />
    ) :(
      <Route {...props} />
    )  
      
  }

  return currentUser ? <Route {...props}/> : <Redirect to={{
    pathname: '/login',
    state:{'from':path},
  }}/>
}